import React, { Component } from 'react';
import {withRouter} from 'react-router-dom';
import {get} from '../../api_client';
import './DashboardData-styles.css'

function capitalizeFirstLetter(string) {
    return string.charAt(0).toUpperCase() + string.slice(1);
}

class DashboardData extends Component {
  constructor(props){
    super(props);

    this.goToRegionPage = this.goToRegionPage.bind(this)
    this.goToHouseholdPage = this.goToHouseholdPage.bind(this)
    this.applyDataOnState = this.applyDataOnState.bind(this)
  };

  componentDidMount(){

  }

  goToHouseholdPage(event){
    let id = event.target.id
    this.props.updateState('house_id', id)
    this.props.history.push('/house')
  }

  goToRegionPage(event){
    event.preventDefault()
    let region_type = event.target.name;
    let id = event.target.id;
    let resourceType = this.props.resourceType
    let name = event.target.textContent;
    console.log(name)
    get(`api/v1/areas/${region_type}/${id}/${resourceType}`)
      .then(data => this.applyDataOnState(data, name, region_type))
      .catch(error => console.log(error))
  }

  applyDataOnState(region, name, region_type){
    let path = '/regionPage/'
    localStorage.setItem('region_type', region_type)
    localStorage.setItem('region_id', region.id)
    localStorage.setItem('region_name', name)
    localStorage.setItem('region_parent', region.parent)
    // console.log(region, id, name, resourceType)
    this.props.updateState('region_type', region_type)
    this.props.updateState('region_id', region.id)
    this.props.updateState('region_name', name)
    this.props.updateState('region_parent', region.parent)
    this.props.updateState('resource_type', this.props.resourceType)
    this.props.history.push(path)
  }

  render() {
    return (
      <div className="data-container">
        {this.props.resourceType !== 'gas' ? <GlobalSavings {...this.props}/> : null}
        <div className="data-item primary">
          <h6 className="data-label">Personal {capitalizeFirstLetter(this.props.resourceType)} Savings </h6>
          <h6 className="data-value primary">{ this.props.data.personal_savings_to_date }</h6>
        </div>
        <div className="regional-header">
          <h5>Regional Rankings ({this.props.data.metric_sym})</h5>
        </div>
        <div className="data-item">
          <a id={this.props.data.household.id} className="data-label" onClick={this.goToHouseholdPage}>Household</a>
          <h6 className="data-value">{ this.props.data.household_total_savings }</h6>
        </div>
        <div className="data-item">
          <a id={this.props.data.neighborhood[0]} name="neighborhoods" className="data-label" onClick={this.goToRegionPage}>{this.props.data.neighborhood[1]}</a>
          <h6 className="data-value">{ this.props.data.neighborhood_total_savings }</h6>
        </div>
        <div className="data-item">
          <a id={this.props.data.city[0]} name="cities" className="data-label" onClick={(e) => this.goToRegionPage(e)}>{this.props.data.city[1]}</a>
          <h6 className="data-value">{ this.props.data.city_total_savings }</h6>
        </div>
        <div className="data-item">
          <a id={this.props.data.region[0]} name="regions" className="data-label" onClick={this.goToRegionPage}>{this.props.data.region[1]}</a>
          <h6 className="data-value">{ this.props.data.region_total_savings }</h6>
        </div>
        <div className="data-item">
          <a id={this.props.data.country[0]} name="countries" className="data-label" onClick={this.goToRegionPage}>{this.props.data.country[1]}</a>
          <h6 className="data-value">{ this.props.data.country_total_savings }</h6>
        </div>
      </div>
    )
  };
};

const GlobalSavings = (props) => {
  return(
    <div className="data-item primary">
      <h6 className="data-label">Global {capitalizeFirstLetter(props.resourceType)} Savings </h6>
      <h6 className="data-value primary">{ props.data.global_collective_savings }</h6>
    </div>
  )
}

export default withRouter(DashboardData);



